// 第三方
import axios from 'axios';
import Cookies from 'js-cookie';
import urlParse from 'url-parse';
import queryString from 'query-string';
import cryptoMd5 from 'crypto-js/md5.js';

// 组件库
import log from 'npm-js-log';
import type from 'npm-js-type';
import runtime from 'npm-js-runtime';

/**
 * @module npm-js-ajax
 * @desc 用于请求接口，包含多环境处理，server端和client都可使用
 * @author swg [源码地址](http://gitlab.sys.bandubanxie.com/fe/npm/tree/master/npm-js-ajax)
 */
export default {
  /**
   * 上行参数加密
   * @desc
   * 客户端加密：1.对原始数据进行排序 2.对1的结果进行加盐计算md5 3.上传原始数据&sign=md5
   * 服务端验证：1.对收到的参数进行排序（不包括sign） 2.对1的结果进行加盐计算md5 3.将2的结果与sign比较
   * @param {Object} options 上行参数
   * @returns {String} 加密后的数据
   */
  encrypt (options = {}) {
    // 所有参数名
    let keys = Object.keys(options);

    // 原始数据，格式为key=value形式的序列化数据（例如：ts=1541414756205&sk=wefweow342423&a=1&c=%e4%b8%89&b=2）
    let originalBody = keys.map(key => `${key}=${encodeURIComponent(options[key])}`).join('&');

    // 对原始数据字典排序（排序后格式为：a=1&b=2&c=%e4%b8%89&sk=wefweow342423&ts=1541414756205）
    // let sortedBody = keys.sort().map(key => `${key}=${encodeURIComponent(options[key])}`).join('&');

    // md5盐
    let salt = 'mengmengda';

    // 对排序后端数据加盐计算md5值
    // let md5 = cryptoMd5(sortedBody + salt).toString();

    let md5 = cryptoMd5(originalBody + salt).toString();

    // 最终数据（格式为[原始数据]&sign=md5：ts=1541414756205&sk=wefweow342423&a=1&c=%e4%b8%89&b=2&sign=md5）
    let finalBody = `${originalBody}&sign=${md5}`;

    return finalBody;
  },

  /**
   * 异步请求接口
   * @param {Object} options axios的参数都支持
   * @param {Number} options.interfaceType 接口类型：-1=老的非jsonp接口，0=请求老的jsonp接口，不传|1=请求新的加密方式接口
   * @param {Object} options.req express的request对象，server端必传
   * @param {Object} options.res express的response对象，server端必传
   * @return {Promise}
   * @demo
   * ajax.send({
      interfaceType: 0,
      method: `get`,
      url: `https://news.baidu.com/sn/api/feed_feedlist`,
      params: {},
      headers: {},
    });
   * @see https://github.com/axios/axios
   */
  async send (options = {}) {
    // 自定义参数 覆盖-> 默认参数
    options = Object.assign({}, {
      method: 'post',
      url: '',
      params: {},
      headers: {},
      globalCb: '',
      interfaceType: 1,
      responseType: 'json',
      timeout: 10000,
      withCredentials: true,
      /* // 接口代理
       proxy: {
       host: 'localhost',
       port: 8888,
       },*/
    }, options);

    // 保留express属性
    let req = options.req;
    let res = options.res;

    // 接口里不需要express属性
    delete options.req;
    delete options.res;

    // 请求老的ajax或jsonp接口
    if (options.interfaceType === -1) {
      options.responseType = 'json';
    } else if (options.interfaceType === 0) {
      options.responseType = 'text';
      options.params.callback = 'no_need_jsonp';
    }

    // 数据加密
    if (options.interfaceType === 1 && !options.data) {
      // 基础公参
      let obj = {
        // timestamp
        ts: Date.now(),
      };

      // 统计公参
      if (runtime.isClient()) {
        let query = queryString.parse(location.search);
        for (let i of ['rp', 'rl', 'logDp', 'dp', 'cq', 'cu']) {
          if (query[i]) {
            obj[i] = query[i];
          }
        }
      }

      // 业务参数
      for (let i in options.params) {
        let val = options.params[i];
        // 只有JSON数据类型才能作为参数
        if (type.isString(val) || type.isNumber(val) || type.isBoolean(val)) {
          obj[i] = val;
        }
      }
      delete options.params;

      // // 如果是服务端，获取用户请求中的dvdsid来设置sess_key
      // if (runtime.isServer()) {
      //   if (req.cookies && req.cookies.sess_key) {
      //     /* eslint-disable */
      //     obj.sess_key = req.cookies.sess_key;
      //   }
      // }

      // 设置加密后的body数据
      options.data = this.encrypt(obj);
    }

    // 处理url对象
    let url = urlParse(options.url);
    url.slashes = true;
    if (runtime.isServer()) {
      if (req) {
        url.protocol = req.protocol;
      }
    } else {
      url.protocol = location.protocol.replace(':', '');
    }

    /*// 针对api.yunshuxie.com域名做特殊处理
    if (url.host.startsWith('api.yunshuxie.com')) {
      if (runtime.isServer()) {
        if (req.cookies.env_flag === 'beta') {
          url.host = `beta.${url.host}`;
        } else if (req.cookies.env_flag === 'stage') {
          url.host = `stage.${url.host}`;
        }
      } else {
        let env_flag = Cookies.get('env_flag', {
          domain: '.yunshuxie.com',
          path: '/',
        });
        if (env_flag === 'beta') {
          url.host = `beta.${url.host}`;
        } else if (env_flag === 'stage') {
          url.host = `stage.${url.host}`;
        }
      }
    }*/

    // 如果是服务端
    if (runtime.isServer()) {
      // 调接口时需要透传的header
      const ReqHeaderKeyList = [
        // 包含用户信息的header字段
        'Cookie',
        'User-Agent',
      ];
      if (req) {
        for (let reqHeaderKey of ReqHeaderKeyList) {
          // express的req.headers中key是小写的
          let header = req.headers[reqHeaderKey.toLowerCase()];

          // 优先使用传入的header，其次使用req中的header
          if (header && !options.headers[reqHeaderKey]) {
            options.headers[reqHeaderKey] = header;
          }
        }
      }

      // 内网私有域名
      if (runtime.isLinux()) {
        url.host = `pri.${url.host}`;
      }
    }

    // 转url字符串
    options.url = url.toString();

    // 防止出错时查不到请求参数
    if (runtime.isServer()) {
      log.log({
        req,
        // content: `接口(${options.url})请求参数：${JSON.stringify(options, ' ', 2)}`,
        content: `接口 [${options.url}] 请求参数：${JSON.stringify(options)}`,
      });
    }

    // 发送请求
    let response = null;
    let start = Date.now();
    try {
      response = await axios.request(options);

      // 返回状态码非200，重试
      if (response.status !== 200) {
        response = await axios.request(options);
      }

      // 返回状态码非200，重试
      if (response.status !== 200) {
        response = await axios.request(options);
      }

      // 返回状态码非200，重试
      if (response.status !== 200) {
        let error = new Error('接口返回码不是200，已主动抛出异常');
        error.response = response;
        throw error;
      }

      // 成功log
      if (runtime.isServer()) {
        log.log({
          req,
          content: `接口 [${options.url}] 请求发送 -> 返回共耗时：${Date.now() - start}ms`,
        });
      }
    } catch (err) {
      // 失败log
      if (runtime.isServer()) {
        err.message = `接口请求超时：${err.message} ${JSON.stringify(options)}`;
      } else {
        if (err.code === 'ECONNABORTED') {
          err.message = '请求超时，请检查网络！';
        } else if (err.message === 'Network Error') {
          err.message = '网络出错，请检查网络！';
        }
      }

      throw err;
    }

    if (runtime.isServer()) {
      // 接口信息，只有request属性没有打印（request内部多数属性都是对象和方法，信息类型的属性很少）
      log.log({
        req,
        content: `接口请求信息（${options.url}）：${JSON.stringify({
          // 请求信息
          config: response.config,
          // 返回信息
          status: response.status,
          statusText: response.statusText,
          headers: response.headers,
          data: response.data,
          // }, ' ', 2)}`,
        })}`,
      });

      // 页面返回时需要透传的header
      const ResHeaderKeyList = ['set-cookie'];
      for (let resHeaderKey of ResHeaderKeyList) {
        let header = response.headers[resHeaderKey];
        if (header) {
          res.set(resHeaderKey, header);
        }
      }
    }

    // 本地环境返回的资源地址https转http
    if (!runtime.isLinux()) {
      // 获取当前页面协议
      let protocol = null;
      if (runtime.isServer()) {
        if (req) {
          protocol = req.protocol;
        }
      } else {
        protocol = location.protocol.replace(':', '');
      }

      // https -> http
      if (protocol === 'http') {
        if (type.isString(response.data)) {
          response.data = response.data.replace(/https/g, 'http');
        } else if (type.isObject(response.data)) {
          response.data = JSON.parse(JSON.stringify(response.data).replace(/https/g, 'http'));
        }
      }
    }

    // 请求老的jsonp接口
    if (options.interfaceType === 0) {
      try {
        response.originalData = response.data;
        response.data = JSON.parse(response.data.replace(/no_need_jsonp\((.+)\)/, '$1'));
      } catch (err) {
        // 失败log
        if (runtime.isServer()) {
          log.error({
            req,
            content: `调用接口发生错误：(${options.url})`,
          });
          log.error({
            req,
            content: err,
          });
        }

        throw new Error('interface');
      }
    }

    // 返回接口结果
    return response;
  },
};
