import time from 'npm-js-time';
import runtime from 'npm-js-runtime';

/**
 * @module npm-js-log
 * @desc 服务端日志打印模块，按照统一规范打印日志
 * @author swg [源码地址](http://gitlab.sys.bandubanxie.com/fe/npm/tree/master/npm-js-log)
 */
export default {
  /**
   * 获取当前时间格式化后的标准时间
   * @returns {string}
   */
  getFormatTime () {
    return time.format(new Date(), 'yyyy-MM-dd hh:mm:ss SSS');
  },

  /**
   * 获取log前缀（前缀包括时间、日志类型、用户信息等）
   * @param type 日志类型
   * @returns {string}
   */
  getLogPrefix (type, {req} = {}) {
    let userInfo = null;
    if (Define.isServer) {
      userInfo = this.decryptUserInfoFromCookie(req);
    }
    return `>>> [${this.getFormatTime()}] --- [${type}] --- [${req.method}] --- [URL=${req.protocol}://${req.headers.host}${req.url}] --- [Referer=${req.headers.referer}] --- [User-Agent=${req.headers['user-agent']}] --- [Cookie=${req.headers['cookie']}] --- [Phone=${userInfo && userInfo.phone}] ===>>>`;
  },

  /**
   * 获取log后缀
   * @param type
   * @returns {string}
   */
  getLogSuffix (type, {req} = {}) {
    return `<<<===`;
  },

  /**
   * 打印日志
   * @param {Object} options.req express的request对象
   * @param {String|Object} options.content 日志内容
   */
  log ({req, content} = {}) {
    if (runtime.isServer() && req) {
      console.log(this.getLogPrefix('log', {req}), content, this.getLogSuffix('log', {req}));
    } else {
      console.log(content);
    }
  },

  /**
   * 打印错误
   * @param {String|Object} options.content 日志内容
   * @param {Object} options.req express的request对象
   */
  error ({req, content} = {}) {
    if (runtime.isServer() && req) {
      console.error(this.getLogPrefix('error', {req}), content, this.getLogSuffix('log', {req}));
    } else {
      console.log(content);
    }
  },

  /**
   * 打印信息
   * @param {String|Object} options.content 日志内容
   * @param {Object} options.req express的request对象
   */
  info ({req, content} = {}) {
    if (runtime.isServer() && req) {
      console.info(this.getLogPrefix('text', {req}), content, this.getLogSuffix('log', {req}));
    } else {
      console.log(content);
    }
  },

  /**
   * 从http请求的cookie中获取用户信息
   * @param req
   */
  decryptUserInfoFromCookie (req) {
    if (Define.isServer) {
      try {
        if (req.cookies.sess_data) {
          let crypto = require('crypto');

          let decipher = crypto.createDecipheriv('aes-128-cbc', 'yunshuxie.com129', '0507088888881029');
          decipher.setAutoPadding(true);

          let cipherChunks = [];
          cipherChunks.push(decipher.update(req.cookies.sess_data, 'base64', 'utf8'));
          cipherChunks.push(decipher.final('utf8'));

          let userInfoStr = cipherChunks.join('');
          let userInfo = JSON.parse(userInfoStr);
          return userInfo;
        } else {
          return null;
        }
      } catch (err) {
        console.error(`解密sess_data出错，sess_data:${req.cookies.sess_data}，SessionKey:${req.cookies.SessionKey}，下面一行是详细错误原因↓`);
        console.error(err);
      }
    }
  },
};


