/**
 * @module npm-js-number
 * @desc 用作处理数字
 * @author swg [源码地址](http://gitlab.sys.bandubanxie.com/fe/npm/tree/master/npm-js-number)
 */
export default {
  /**
   * 数字前面补0
   * @param {Number|String} value 需要补0的数字
   * @param {Number} digit 补0后的位数
   * @return {String}
   */
  prefixZero (value = '', totalDigit = 1) {
    // 指定的总位数没有比当前数字大，直接范围当前数字的字符串形式
    if (totalDigit <= value.toString().length) {
      return value;
    }

    // 计算需要补充的0的数量
    let preZeroNum = totalDigit - value.toString().length;

    // 生成前置0
    let prefix = [];
    for (let i = 0; i < preZeroNum; i++) {
      prefix.push('0');
    }

    // 拼接前置0+数字返回
    return prefix.join('') + value;
  },

  /**
   * 浮点数计算-加法
   * @param {Number} a 数字a
   * @param {Number} b 数字b
   * @param {Number} digit 有效位数（有效位之后的数字将被丢弃不予计算）
   * @returns {Number}
   */
  plus (a = 0, b = 0, digit = 2) {
    let expand = Math.pow(10, digit);
    return (parseInt(a * expand) + parseInt(b * expand)) / expand;
  },

  /**
   * 浮点数计算-减法
   * @param {Number} a 数字a
   * @param {Number} b 数字b
   * @param {Number} digit 有效位数（有效位之后的数字将被丢弃不予计算）
   * @returns {Number}
   */
  minus (a = 0, b = 0, digit = 2) {
    let expand = Math.pow(10, digit);
    return (parseInt(a * expand) - parseInt(b * expand)) / expand;
  },

  /**
   * 浮点数计算-乘法
   * @param {Number} a 数字a
   * @param {Number} b 数字b
   * @param {Number} digit 有效位数（有效位之后的数字将被丢弃不予计算）
   * @returns {Number}
   */
  multiply (a = 0, b = 0, digit = 2) {
    let expand = Math.pow(10, digit);
    return (parseInt(a * expand) * parseInt(b * expand)) / expand;
  },

  /**
   * 浮点数计算-除法
   * @param {Number} a 数字a
   * @param {Number} b 数字b
   * @param {Number} digit 有效位数（有效位之后的数字将被丢弃不予计算）
   * @returns {Number}
   */
  divide (a = 0, b = 0, digit = 2) {
    let expand = Math.pow(10, digit);
    return (parseInt(a * expand) / parseInt(b * expand)) / expand;
  },
};
